const resultHit = hit => {
  const star = hit.rating <= 1 ? 'star' : 'stars';

  return `<div class="result-hit">
            <a href=${hit.url} target="_blank" class="result-hit__name">
              <div class="result-hit__image-container">
                <img class="result-hit__image" src="${hit.image}" />
              </div>
              <div class="result-hit__details">
                <h3>${hit.name}</h3>
                <p class="result-hit__price">$${hit.price} |      
                Rating: ${hit.rating} ${star}</p>
              </div>
            </a>
          </div>`;
};

export default resultHit;
