import ResultsPage from './components/results-page';
import Autocomplete from './components/autocomplete';

class SpencerAndWilliamsSearch {
  constructor() {
    this._initSearch();
    this._registerEvents();
  }

  _initSearch() {
    this.autocompleteDropdown = new Autocomplete();
    this.resultPage = new ResultsPage();
  }

  _registerEvents() {
    let autocomplete = document.querySelector('.autocomplete');
    const searchbox = document.querySelector('#searchbox input');

    searchbox.addEventListener('click', () => {
      // const autoCompleteHits = document.getElementById('autocomplete-hits');
      // console.log(`autoCompleteHits: ${typeof autoCompleteHits}`);
      autocomplete.style.display = 'block';
    });

    // Note: I did not figure out how to set the click not only outside the searchbox, but
    // also outside autocomplete's dropdown (AKA <a href> link does not work in autocomplete)
    searchbox.addEventListener('blur', () => {
      autocomplete.style.display = 'none'; // Hide the element
    });
  }
}

const app = new SpencerAndWilliamsSearch();
