/**
 * Key note(s):
 * (1) For refinementList: must add its attribute for faceting (e.g. via Algolia's dashboard);
 * otherwise, nothing will show on the UI and no (API) error message anywhere
 * (2) For refinementList: 'templates' is optional (AKA not required) parameter
 * (3) For sortBy: requires repliace of indices
 */

import algoliasearch from 'algoliasearch';
import instantsearch from 'instantsearch.js';
import { appID, adminApiKey } from '../../config'; // API keys

import {
  hits,
  pagination,
  refinementList,
  ratingMenu,
  sortBy,
} from 'instantsearch.js/es/widgets';

import resultHit from '../templates/result-hit';

/**
 * @class ResultsPage
 * @description Instant Search class to display content on main page
 */
class ResultPage {
  constructor() {
    this._registerClient();
    this._registerWidgets();
    this._startSearch();
  }

  /**
   * @private
   * Handles creating the search client and creating an instance of instant search
   * @return {void}
   */
  _registerClient() {
    this._searchClient = algoliasearch(appID, adminApiKey);

    this._searchInstance = instantsearch({
      indexName: 'objectID',
      searchClient: this._searchClient,
    });
  }

  /**
   * @private
   * Adds widgets to the Algolia instant search instance
   * @return {void}
   */
  _registerWidgets() {
    this._searchInstance.addWidgets([
      hits({
        container: '#hits',
        templates: {
          item: resultHit, // Use JS to generate HTML
        },
      }),
      pagination({
        container: '#pagination',
      }),
      refinementList({
        container: '#brand-facet',
        attribute: 'brand',
        showMore: true,
        showMoreLimit: 20,
      }),
      refinementList({
        container: '#categories-facet',
        attribute: 'categories',
        showMore: true,
        showMoreLimit: 40,
      }),
      ratingMenu({
        container: '#rating-facet',
        attribute: 'rating',
      }),
      sortBy({
        container: '#sort-by',
        items: [
          { label: 'Default', value: 'objectID' },
          { label: 'Price: Low to High', value: 'price_asc' },
          { label: 'Price: High to Low', value: 'price_desc' },
          { label: 'Rating: Low to High', value: 'rating_asc' },
          { label: 'Rating: High to Low', value: 'rating_desc' },
        ],
      }),
    ]);
  }

  /**
   * @private
   * Starts instant search after widgets are registered
   * @return {void}
   */
  _startSearch() {
    this._searchInstance.start();
  }
}

export default ResultPage;
