/**
 * Key note(s):
 * (1) More than 1 hit (e.g. both hits here and resul page's hits) can be add to
 * 'this._searchInstance.addWidgets()'
 */

import algoliasearch from 'algoliasearch';
import instantsearch from 'instantsearch.js';
import { appID, adminApiKey } from '../../config'; // API keys

// Instant Search Widgets
import { hits, searchBox, configure } from 'instantsearch.js/es/widgets';

// Autocomplete Template
import autocompleteProductTemplate from '../templates/autocomplete-product';

/**
 * @class Autocomplete
 * @description Instant Search class to display content in the page's autocomplete
 */
class Autocomplete {
  /**
   * @constructor
   */
  constructor() {
    this._registerClient();
    this._registerWidgets();
    this._startSearch();
  }

  /**
   * @private
   * Handles creating the search client and creating an instance of instant search
   * @return {void}
   */
  _registerClient() {
    this._searchClient = algoliasearch(appID, adminApiKey);

    this._searchInstance = instantsearch({
      indexName: 'objectID',
      searchClient: this._searchClient,
    });
  }

  /**
   * @private
   * Adds widgets to the Algolia instant search instance
   * @return {void}
   */
  _registerWidgets() {
    this._searchInstance.addWidgets([
      configure({
        // Note: this number should be small, to be fully displayed on the page
        hitsPerPage: 3,
      }),
      searchBox({
        container: '#searchbox',
        // searchAsYouType: false, // AKA search on enter
        showSubmit: true,
      }),
      hits({
        container: '#autocomplete-hits',
        templates: { item: autocompleteProductTemplate }, // Use JS to generate HTML
      }),
    ]);
  }

  /**
   * @private
   * Starts instant search after widgets are registered
   * @return {void}
   */
  _startSearch() {
    this._searchInstance.start();
  }
}

export default Autocomplete;
